@import "../../theme/theme";

.contact-wrapper {
  padding: 3% 0 1% 0;
  padding-top: 0;
  background: $dark-bg;
  h1,
  label,
  h5,
  h4,
  .post-content {
    color: $white;
  }
}

.partner-logos {
  // display: flex;
  // flex-wrap: wrap;
  a {
    flex: 0 50%;
    margin: 10px;
    img {
      padding: 9px 0;
    }
  }
}

.success-message {
  border: 1px solid $red;
  padding: 24px 10px 9px 10px;
  text-align: center;
  span {
    color: $orange;
  }
}

.with-bottom-border {
  border-bottom: 0.5px solid $grey-text;
  margin-bottom: 13px;
}
