@import url("https://fonts.googleapis.com/css?family=Poppins&display=swap");

$dark-grey: #2d3436;
$light-grey: #505050;
$grey-text: #888887;
$red: #d82545;
$orange: #feac58;
$purple: #273c67;
$dark-bg: #1e1e1e;
$white: #ffffff;
$title-grey: #6c1e2c;

body,
html,
li,
a,
h1,
h2,
h3,
h4,
h5,
h6,
label,
p,
span {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
}

.arrow-down {
  width: 0;
  height: 0;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  position: relative;
  top: -50%;
  left: 10%;
  padding-bottom: 2%;
  position: relative;
  top: -1px;
}

.pre-title {
  color: $white;
  font-size: 1.5em;
  font-weight: 500;
}

.title {
  font-weight: 900;
  color: $title-grey;
  font-size: 5em;
  margin-bottom: 0.3em;
}
.sub-title,
p {
  color: $white;
}

.uc {
  text-transform: uppercase;
}

.post-content {
  font-size: 1.8em;
}

.wrap {
  padding: 3% 0;
  padding-top: 0;
}

.services-list li {
  font-weight: 900;
  font-size: 1.5em;
}

.custom-link {
  color: $white;
  &:hover {
    color: $grey-text;
    text-decoration: none;
  }
}

.hasError {
  color: $red;
  font-size: 0.9em;
  font-weight: bold;
}

.btn-outline-white {
  color: #fff !important;
  border-color: #fff !important;
  &:hover {
    color: #444 !important;
    text-decoration: none !important;
    background: #eee !important;
    border-color: #eee !important;
  }
}

.card {
  border: none !important;
}

.modal-footer {
  padding: 0.3em !important;
}

.modal-header {
  padding: 0.4em 0.8em !important;
  button {
    position: relative;
    top: 6px;
  }
}
