@import "../../theme/theme";

.mobileMenuWrap {
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  li {
    margin: 12px 0;
    a {
      color: $white;
      background: $dark-grey;
      margin: 5px 0;
      border-radius: 0 5px 5px 0;
      padding: 5px 15px 5px 15px;
      font-size: 0.9em;
      font-weight: 300;
      &:hover {
        background: $light-grey;
        text-decoration: none;
      }
    }
  }
  .navigate {
    position: relative;
    left: 8px;
    max-width: 100px;
  }
}
